:root {
  --main-color: sandybrown;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  background-color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--main-color);
}

.App {
  font-family: sans-serif;
  text-align: center;
}




